import React, { useState } from 'react';
import {
  Box,
  Typography,
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Chip,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Add as AddIcon,
  Visibility as VisibilityIcon,
  Delete as DeleteIcon,
  FileDownload as FileDownloadIcon,
} from '@mui/icons-material';
import { QuoteTemplate } from '../../types/quoteTemplate';

interface QuotationTemplatesProps {
  templates: QuoteTemplate[];
  loading: boolean;
  onUploadClick: () => void;
  onViewTemplate?: (templateId: string) => void;
  onDeleteTemplate?: (templateId: string) => void;
  onDownloadTemplate?: (templateId: string) => void;
}

const QuotationTemplates: React.FC<QuotationTemplatesProps> = ({
  templates,
  loading,
  onUploadClick,
  onViewTemplate,
  onDeleteTemplate,
  onDownloadTemplate,
}) => {
  return (
    <Paper sx={{ p: 3 }}>
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h6">Mu bo gi</Typography>
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={onUploadClick}
        >
          Ti ln mu bo gi
        </Button>
      </Box>

      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Tn mu</TableCell>
              <TableCell>M t</TableCell>
              <TableCell>Loi</TableCell>
              <TableCell>Trng thi</TableCell>
              <TableCell>Ngy to</TableCell>
              <TableCell>Ngi to</TableCell>
              <TableCell>S trng</TableCell>
              <TableCell align="center">Thao tc</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <TableRow>
                <TableCell colSpan={8} align="center">
                  ang ti...
                </TableCell>
              </TableRow>
            ) : templates.length === 0 ? (
              <TableRow>
                <TableCell colSpan={8} align="center">
                  Cha c mu bo gi no. Hy ti ln mu bo gi u tin ca bn!
                </TableCell>
              </TableRow>
            ) : (
              templates.map((template) => (
                <TableRow key={template.id}>
                  <TableCell>{template.name}</TableCell>
                  <TableCell>{template.description || '-'}</TableCell>
                  <TableCell>
                    {template.type === 'customer' ? 'Khch hng' : 'Ni b'}
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={template.status === 'active' ? 'ang s dng' : 'Khng s dng'}
                      color={template.status === 'active' ? 'success' : 'default'}
                      size="small"
                    />
                    {template.isDefault && (
                      <Chip
                        label="Mc nh"
                        color="primary"
                        size="small"
                        sx={{ ml: 1 }}
                      />
                    )}
                  </TableCell>
                  <TableCell>
                    {new Date(template.createdAt).toLocaleDateString('vi-VN')}
                  </TableCell>
                  <TableCell>{template.createdBy}</TableCell>
                  <TableCell>{template.placeholderCount}</TableCell>
                  <TableCell align="center">
                    <Box>
                      {onViewTemplate && (
                        <Tooltip title="Xem chi tit">
                          <IconButton
                            size="small"
                            onClick={() => onViewTemplate(template.id)}
                          >
                            <VisibilityIcon fontSize="small" />
                          </IconButton>
                        </Tooltip>
                      )}
                      {onDownloadTemplate && (
                        <Tooltip title="Ti xung">
                          <IconButton
                            size="small"
                            onClick={() => onDownloadTemplate(template.id)}
                          >
                            <FileDownloadIcon fontSize="small" />
                          </IconButton>
                        </Tooltip>
                      )}
                      {onDeleteTemplate && (
                        <Tooltip title="Xa">
                          <IconButton
                            size="small"
                            color="error"
                            onClick={() => onDeleteTemplate(template.id)}
                          >
                            <DeleteIcon fontSize="small" />
                          </IconButton>
                        </Tooltip>
                      )}
                    </Box>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
    </Paper>
  );
};

export default QuotationTemplates;
