import React, { useState } from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  IconButton,
} from '@mui/material';
import { Close as CloseIcon, Visibility as PreviewIcon, Send as SendIcon } from '@mui/icons-material';

interface EmailQuotationDialogProps {
  open: boolean;
  onClose: () => void;
  onSend: (emailData: {
    to: string;
    cc: string;
    subject: string;
    content: string;
  }) => void;
  customerName: string;
  serviceName: string;
  responsiblePerson: string;
  pdfFileName: string;
}

const EmailQuotationDialog: React.FC<EmailQuotationDialogProps> = ({
  open,
  onClose,
  onSend,
  customerName,
  serviceName,
  responsiblePerson,
  pdfFileName,
}) => {
  const [emailData, setEmailData] = useState({
    to: '',
    cc: '',
    subject: `Bo gi dch v ${serviceName}`,
    content: `Knh gi Anh/Ch ${customerName || 'Qu khch'},

Innotech Vietnam xin gi Anh/Ch bo gi cho dch v ${serviceName || 'dch v'} nh nh km.

Nu cn thm thng tin, xin vui lng lin h chng ti.

Trn trng,  
${responsiblePerson || 'i ng Innotech Vietnam'}`,
  });

  const handleChange = (field: string) => (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setEmailData(prev => ({
      ...prev,
      [field]: e.target.value,
    }));
  };

  const handleSend = () => {
    onSend(emailData);
    onClose();
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>
        <Box display="flex" justifyContent="space-between" alignItems="center">
          <Typography variant="h6">Gi bo gi cho khch hng</Typography>
          <IconButton onClick={onClose} size="small">
            <CloseIcon />
          </IconButton>
        </Box>
      </DialogTitle>
      <DialogContent dividers>
        <Box mb={3}>
          <TextField
            fullWidth
            label="n (Email)"
            value={emailData.to}
            onChange={handleChange('to')}
            margin="normal"
            variant="outlined"
          />
          
          <TextField
            fullWidth
            label="CC (Ty chn)"
            value={emailData.cc}
            onChange={handleChange('cc')}
            margin="normal"
            variant="outlined"
          />
          
          <TextField
            fullWidth
            label="Tiu "
            value={emailData.subject}
            onChange={handleChange('subject')}
            margin="normal"
            variant="outlined"
          />
          
          <Box mt={2} mb={2} display="flex" alignItems="center">
            <Typography variant="body1" sx={{ mr: 2 }}>
              File nh km: {pdfFileName}
            </Typography>
            <IconButton size="small" onClick={() => window.open(`/preview/${pdfFileName}`, '_blank')}>
              <PreviewIcon />
            </IconButton>
          </Box>
          
          <TextField
            fullWidth
            label="Ni dung email"
            value={emailData.content}
            onChange={handleChange('content')}
            margin="normal"
            variant="outlined"
            multiline
            rows={8}
          />
        </Box>
      </DialogContent>
      <DialogActions sx={{ p: 2 }}>
        <Button onClick={onClose}>Hy</Button>
        <Button
          variant="contained"
          color="primary"
          startIcon={<SendIcon />}
          onClick={handleSend}
          disabled={!emailData.to}
        >
          Gi email
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default EmailQuotationDialog;
