import React, { useState, useEffect } from 'react';
import { PdfPageEditor } from '../../../../components/PdfPageEditor';
import { 
  Button, 
  Dialog, 
  DialogActions, 
  DialogContent, 
  DialogTitle, 
  Typography,
  Box 
} from '@mui/material';
import { Save as SaveIcon } from '@mui/icons-material';
import { QuotationPreviewProps } from '../../types';

const QuotationPreview: React.FC<QuotationPreviewProps> = ({
  open,
  onClose,
  quotation,
  onSaveDraft,
  onExportPdf,
}) => {
  const [pages, setPages] = useState<string[]>(['']);

  useEffect(() => {
    if (quotation) {
      const customPages = (quotation as any).pages;
      if (customPages && Array.isArray(customPages) && customPages.length > 0) {
        setPages(customPages);
      } else {
        setPages([
          `
          <div style="padding: 20mm;">
            <h1 style="text-align: center; margin-bottom: 30px;">BO GI DCH V</h1>
            <div style="display: flex; justify-content: space-between; margin-bottom: 20px;">
              <div>
                <p><strong>Khch hng:</strong> ${quotation.customer || 'Qu khch hng'}</p>
                <p><strong>D n:</strong> ${quotation.name || 'D n phn mm'}</p>
              </div>
              <div style="text-align: right;">
                <p><strong>M bo gi:</strong> ${quotation.id || 'N/A'}</p>
                <p><strong>Ngy to:</strong> ${new Date(quotation.createdDate || new Date()).toLocaleDateString('vi-VN')}</p>
              </div>
            </div>
            
            <!-- Quotation items table -->
            <div style="margin: 20px 0;">
              <h3>CHI TIT DCH V</h3>
              <table style="width: 100%; border-collapse: collapse; margin: 15px 0;">
                <thead>
                  <tr style="background-color: #f5f5f5;">
                    <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">STT</th>
                    <th style="border: 1px solid #ddd; padding: 8px; text-align: left;">M t cng vic</th>
                    <th style="border: 1px solid #ddd; padding: 8px; text-align: right;">S ngy cng</th>
                    <th style="border: 1px solid #ddd; padding: 8px; text-align: right;">n gi (VND)</th>
                    <th style="border: 1px solid #ddd; padding: 8px; text-align: right;">Thnh tin (VND)</th>
                  </tr>
                </thead>
                <tbody>
                  ${(quotation.items || []).map((item, index) => {
                    const qcEffort = (item as any).qcEffort || 0;
                    const rate = (item as any).rate || 0;
                    const total = qcEffort * rate;
                    
                    return `
                      <tr>
                        <td style="border: 1px solid #ddd; padding: 8px;">${index + 1}</td>
                        <td style="border: 1px solid #ddd; padding: 8px;">${item.description || ''}</td>
                        <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">${qcEffort}</td>
                        <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">${rate.toLocaleString('vi-VN')}</td>
                        <td style="border: 1px solid #ddd; padding: 8px; text-align: right;">${total.toLocaleString('vi-VN')}</td>
                      </tr>
                    `;
                  }).join('')}
                  <tr>
                    <td colspan="4" style="border: 1px solid #ddd; padding: 8px; text-align: right;"><strong>Tng cng:</strong></td>
                    <td style="border: 1px solid #ddd; padding: 8px; text-align: right; font-weight: bold;">
                      ${(quotation.items || []).reduce((sum, item) => {
                        const qcEffort = (item as any).qcEffort || 0;
                        const rate = (item as any).rate || 0;
                        return sum + (qcEffort * rate);
                      }, 0).toLocaleString('vi-VN')} VND
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
            
            <div style="margin-top: 30px;">
              <div style="margin-bottom: 20px;">
                <h3>IU KHON THANH TON</h3>
                <div>${(quotation as any).paymentTerms || 'Thanh ton 100% khi k hp ng.'}</div>
              </div>
              <div style="margin-top: 60px; display: flex; justify-content: space-between;">
                <div style="text-align: center; width: 50%;">
                  <p><strong>I DIN BN BN</strong></p>
                  <p style="font-style: italic; margin-top: 50px;">(K, ghi r h tn)</p>
                </div>
                <div style="text-align: center; width: 50%;">
                  <p><strong>I DIN BN MUA</strong></p>
                  <p style="font-style: italic; margin-top: 50px;">(K, ghi r h tn)</p>
                </div>
              </div>
            </div>
          </div>
          `
        ]);
      }
    }
  }, [quotation]);

  const handleSave = (updatedPages: string[]) => {
    setPages(updatedPages);
  };

  const handleSaveDraft = () => {
    if (onSaveDraft && quotation) {
      // Create an updated quotation with the pages
      const updatedQuotation = {
        ...quotation,
        // Add pages to the quotation data
        ...(pages.length > 0 && { pages })
      };
      onSaveDraft(updatedQuotation);
    }
  };

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="xl"
      fullWidth
      sx={{ '& .MuiDialog-paper': { maxWidth: '95%', height: '95vh' } }}
    >
      <DialogTitle>
        <Typography variant="h6">Xem trc v chnh sa bo gi</Typography>
      </DialogTitle>
      <DialogContent sx={{ p: 0, display: 'flex', flexDirection: 'column', height: '100%' }}>
        {quotation ? (
          <PdfPageEditor 
            initialContent={pages}
            onSave={handleSave}
            onExportPdf={() => onExportPdf && onExportPdf(quotation)}
          />
        ) : (
          <Box sx={{ p: 3, textAlign: 'center' }}>
            <Typography>ang ti d liu bo gi...</Typography>
          </Box>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose} color="inherit">
          ng
        </Button>
        <Button 
          onClick={handleSaveDraft} 
          variant="contained" 
          color="primary"
          startIcon={<SaveIcon />}
          disabled={!quotation}
        >
          Lu nhp
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default QuotationPreview;
